module.exports = function init (serviceLocator) {

  serviceLocator.logger.info('Initializing Scroll-to Links')

  var $links = $('[data-scroll]')

  if ($links.length === 0) return

  $links.click(function (e) {
    var $this = $(this)
      , hash = '#' + $this.attr('data-scroll')
      , $target = $(hash)

    if ($target.length > 0) {
      e.preventDefault()

      scrollTo(hash)
    }
  })

  function scrollTo (hash) {

    var $target = $(hash)

    $('body').animate({
      scrollTop: $target.offset().top
    }, 500, function () {
      if (window.location.hash !== hash) {
        window.location.hash = hash
      }
    })

  }

}
