module.exports = function init (serviceLocator) {

  serviceLocator.logger.info('Initializing Adverts')

  if (!window.navigator.onLine) return

  var $adverts = $('.js-advert')
    , $script

  if ($adverts.length === 0) return

  $script = $('<script src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js" async defer></script>')

  $('body').append($script)

  $adverts.each(function () {
    (adsbygoogle = window.adsbygoogle || []).push({})
  }).addClass('has-loaded')

}
