module.exports = function init (serviceLocator) {

  serviceLocator.logger.info('Initializing Redirect')

  var $redirect = $('[data-redirect]')
    , url

  if ($redirect.length === 0) return

  $redirect.addClass('is-vhidden')

  url = $redirect.attr('data-redirect')

  if (window.location.hash) {
    url = url.replace(/#.*$/, window.location.hash)
  }

  if (url.length > 0 && window.location.href !== url) {
    serviceLocator.logger.info('Redirecting to ' + url)

    window.location.href = url
  }

}
