module.exports = function init (serviceLocator) {

  serviceLocator.logger.info('Initializing Homepage')

  var $sections = $('.js-homepage-section')
    , $featured
    , $featuredTemplate
    , $featuredContainer

  if ($sections.length === 0) return

  $sections.each(function () {
    var $this = $(this)
    , $template = $('.js-homepage-section-template', $this)

    $this.replaceWith(function () {
      return $('<' + $this[0].localName + ' class=\'js-homepage-section--replaced\' />').append($template.html())
    })
  })

  $featured = $('.js-homepage-featured')

  if ($featured.length === 0) return

  $featuredTemplate = $('.js-homepage-featured-template')

  $featuredTemplate.replaceWith(function () {
    return $('<section>').append($featuredTemplate.html())
  })

  $featuredContainer = $('.js-homepage-featured-container')

  $featured.sort(function (a, b) {
    return a.getAttribute('data-featured') - b.getAttribute('data-featured')
  }).each(function () {
    var $this = $(this)

    $this.appendTo($featuredContainer)
  })

  $sections = $('.js-homepage-section--replaced')
  $sections.each(function () {
    var $this = $(this)
    , $activities = $('.activity', $this)

    if ($activities.length === 0) $this.addClass('is-hidden')
  })

}
