module.exports = bootstrap

var components =
  [ require('./components/redirect/init')
  , require('./components/homepage/init')
  , require('./components/scroll/init')
  , require('./components/external-images/init')
  , require('./components/advert/init')
  ]

function bootstrap (serviceLocator) {
  components.forEach(function (component) {
    component(serviceLocator)
  })
}
