module.exports = function init (serviceLocator) {

  serviceLocator.logger.info('Initializing External Image Loader')

  if (!window.navigator.onLine) return

  var sourceAttribute = 'data-external-src'
    , $images = $('[' + sourceAttribute + ']')
    , found = {}

  if ($images.length === 0) return

  $images.each(function () {
    var $this = $(this)
      , src = $this.attr(sourceAttribute)
      , style
      , image
      , $matches

    if (!found[src]) {
      found[src] = true

      style = 'background-image:url(' + src + ')'
      image = new Image()

      image.onload = function () {
        $matches = $('[' + sourceAttribute + '="' + src + '"]')

        $matches.each(function () {
          var $match = $(this)

          if ($match.is('[data-external-image]')) {
            $match.attr('src', src).removeAttr('data-external-image')
          }

          if ($match.is('[data-external-background-image]')) {
            $match.attr('style', style).removeAttr('data-external-background-image')
          }

          $match.removeAttr(sourceAttribute)
        })
      }

      image.src = src
    }

  })

}
